.imageContainer {
  /* overflow: hidden; */
  display: block;
  min-width: 0;
  min-height: 0;
}
.imageContainer img {
  display: block;
  object-fit: cover;
  max-width: 100%;
}
.title {
  margin: unset;
}
.videoContainer {
  display: flex;
  aspect-ratio: 16/9;

  padding-bottom: 3vh;
  align-items: center;
  justify-content: start;
  width: 100%;
}

.text {
  margin-bottom: 5vh;
}

.section {
  scroll-snap-align: start;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-gap: 2vh;
  grid-template-columns: 1fr;
  overflow: hidden;
  align-items: center;
  width: var(--contentWidth);
  min-height: 88vh;
  padding-top: 4vh;
  box-sizing: border-box;
}
.container {
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  box-sizing: border-box;
  height: 88vh;
  width: var(--contentWidth);
}
.contentContainer {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: auto;
  color: snow;
  z-index: 0;
  text-align: start;
  line-height: 1.6;
  letter-spacing: 0.1em;
  overflow: hidden;
}
